body{
    margin: 0;
    background-color: #115c6bef;
    font-family: sans-serif;
}
h1{
    margin: 0;
    margin-bottom: 15px;
    color: #a2b8bd7a;/*blend colour*/
}
.todo-container{
    
    /*to align the box to center from here*/
    width: 480px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    /*till here*/
     text-align: center;
}
.input-section{
    background-color:#055565;
    border-radius: 10px;
    padding: 18px;
    box-sizing: border-box;
    box-shadow: 0  7px 6px rgba(10, 10, 10, 90%);
    
   
}


.input-section input{
    background-color:#055565;
    border-radius: 5px;/* to traform box shape means rectangle 
                        to slight bend rectangle*/
    border: 1px solid #056679;/*colour blend clr~called newmorphism*/
    width: 100%;
    /*since the box length is more to reduce it*/
    box-sizing: border-box;
    padding: 15px;
    outline: none; /*to remove outline*/
    color: #08869e;

}

input::placeholder{

/*to change placvehol√∂der colour*/
color: #08869e;

}
ul{
   
    padding:0;
    text-align: left;
}
ul li{
    list-style: none;/*to remove dot*/
    background-color: #055565;
    padding: 15px;
    border-radius: 5px;/* to traform box shape means rectangle 
                        to slight bend rectangle*/
                        margin-bottom: 10px;
    color: #08869e;
    box-shadow: 0  5px 10px rgba(10, 10, 10, 90%);/*box shadow and opacity*/
    /*0-control left right, middle-top to bottom */
    display: flex;/* for trash to center*/
    
    
    
  
}

ul li i{ 
    /*float right is enough but not recommrntded to use 
    meanwhile use: 1, In li : display:flex 
    2, In i text align right , flex 1*/

   text-align: right; /* for trash*/
    flex:1 ; /* for trash*/
    cursor: pointer; 
   
   
}
