header{
    background-color: #055565;
    width: 100%;
    height: 80px;
    display: flex;/*to center*/
    justify-content: center;/*to center*/
    align-items: center;/*to center from top*/
}

header a{
    color: #a2b8bd7a;
    text-decoration: none;/*to remove underline*/
    margin: 0px 15px;/*0 for top bottom and 15 for left right*/
    font-weight: bold;

}